"use client";

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import Image from "next/image";
import styles from './Header.module.css';

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const [admissionOpen, setAdmissionOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [isMounted, setIsMounted] = useState(false);

  const admissionRef = useRef(null);

  const navLinks = [
    {
      name: "ABOUT",
      href: "/",
      dropdown: [
        { name: "About JSS", href: "/" },
        { name: "Heritage", href: "/" },
      ],
    },
    {
      name: "ACADEMICS",
      href: "/",
      dropdown: [
        { name: "Schools", href: "/" },
        { name: "Departments", href: "/" },
        { name: "Programs", href: "/" },
      ],
    },
    {
      name: "ADMISSIONS",
      href: "/",
      dropdown: [
        { name: "Overview", href: "/" },
        { name: "Admission", href: "/" },
        { name: "UG Program", href: "/" },
      ],
    },
    {
      name: "FACILITIES",
      href: "/",
      dropdown: [
        { name: "Girls Hostel", href: "/" },
        { name: "Boys Hostel", href: "/" },
        { name: "Amenities Centre", href: "/" },
      ],
    },
    {
      name: "STUDENTS SUPPORT",
      href: "/",
      dropdown: [
        { name: "Student Life", href: "/" },
        { name: "Mentoring Scheme", href: "/" },
        { name: "Internal Complaint Committee", href: "/" },
      ],
    },
  ];

  const hamburgerMenudata = [
    {
      name: "About JSS University",
      subMenu: ["Overview", "Scholarships", "International Students"],
      img: "/images/hambuger-banner.png",
    },
    {
      name: "Academics",
      subMenu: ["Undergraduate", "Postgraduate", "Doctoral"],
      img: "/images/hambuger-banner.png",
    },
    {
      name: "Facilities",
      subMenu: ["Hostels", "Clubs & Societies", "Events"],
      img: "/images/hambuger-banner.png",
    },
    {
      name: "Students Support",
      subMenu: ["Apply Now", "Eligibility", "FAQs"],
      img: "/images/home-banner.png",
    },
  ];

  const admissionsData = {
    left: {
      subtitle: "JOIN JSSATE NOIDA FOR 2025-26",
      title: "STEP INTO YOUR FUTURE AT JSS NOIDA",
      desc: "Donec pede justo, fringilla vel, aliquet nec, vulputate eget, arcu. In enim justo.",
      email: "principal@jssaten.ac.in",
      phone: "+91-9311830458",
      ctas: [
        { text: "APPLY NOW", href: "#", type: "primary" },
        { text: "DOWNLOAD SYLLABUS", href: "#", type: "secondary" },
      ],
    },
    middle: {
      links: [
        "Scholarship",
        "Course, Eligibility & Fee Structure",
        "Admission Document & Undertaking",
        "Admissions Office Contacts",
        "Hostel Details",
      ],
      stats: {
        text: "1,200+ ACROSS UG & PG PROGRAMS",
        subtext: "Total student intake (annual)",
        btnText: "VIEW PROGRAMMES",
      },
    },
    right: {
      img: "https://picsum.photos/500/400?random=5",
      alt: "Admissions Image",
    },
  };

  useEffect(() => {
    setIsMounted(true);
  }, []);

  useEffect(() => {
    if (!isMounted) return;
    
    const handleScroll = () => {
      setScrolled(window.scrollY > 300);
    };
    
    handleScroll();
    
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [isMounted]);

  const openMenu = () => {
    setMenuOpen(true);
    setActiveIndex(0);
  };
  
  const closeMenu = () => {
    setMenuOpen(false);
    setActiveIndex(null);
  };

  // Close with ESC key
  useEffect(() => {
    if (!isMounted) return;
    
    const handleEsc = (e) => {
      if (e.key === "Escape") closeMenu();
    };
    window.addEventListener("keydown", handleEsc);
    return () => window.removeEventListener("keydown", handleEsc);
  }, [isMounted]);

  // Close admission dropdown when clicking outside
  useEffect(() => {
    if (!isMounted) return;
    
    const handleClickOutside = (e) => {
      if (admissionRef.current && !admissionRef.current.contains(e.target)) {
        setAdmissionOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, [isMounted]);

  const activeData = hamburgerMenudata[activeIndex] || hamburgerMenudata[0];

  // Add a loading state for initial render
  if (!isMounted) {
    return (
      <header className={styles.siteHeader}>
        <div className={styles.navContainer}>
          <div className={styles.brandWrap}>
            {/* Simple static header during SSR */}
          </div>
        </div>
      </header>
    );
  }

  return (
    <header className={styles.siteHeader}>
      <div className={`${styles.navContainer} ${scrolled ? styles.headerScrolled : ''}`}>
        <div className={styles.brandWrap}>
          <Link href="/" aria-label="Home">
            <Image
              src="/images/logo.png"
              alt="Site Logo"
              width={250}
              height={90}
              priority
            />
          </Link>
        </div>

        <div className={styles.rightNavbar}>
          {/* main nav */}
          <nav className={styles.desktopNav} aria-label="Main navigation">
            <ul className={styles.navList}>
              {navLinks.map((l, i) => (
                <li key={i} className={styles.navItem}>
                  <Link href={l.href} className={styles.navLink}>
                    {l.name}
                  </Link>
                  {l.dropdown && (
                    <ul className={styles.dropdown}>
                      {l.dropdown.map((d, j) => (
                        <li key={j}>
                          <Link href={d.href} className={styles.dropdownItem}>
                            {d.name}
                          </Link>
                        </li>
                      ))}
                    </ul>
                  )}
                </li>
              ))}
            </ul>
          </nav>
          <div className={styles.rightNavbarSection}>
            {/* Admissions Button */}
            <div className={styles.admissionWrap} ref={admissionRef}>
              <button
                className={styles.admissionBtn}
                onClick={() => setAdmissionOpen((prev) => !prev)}
              >
                ADMISSIONS
              </button>

              {admissionOpen && (
                <div className={styles.admissionDropdown}>
                  <span className={styles.dropdownArrow}></span>
                  {/* Left */}
                  <div className={styles.adLeft}>
                    <p className={styles.adSubtitle}>
                      {admissionsData.left.subtitle}
                    </p>
                    <h2 className={styles.adTitle}>{admissionsData.left.title}</h2>
                    <p className={styles.adDesc}>{admissionsData.left.desc}</p>
                    <div className={styles.adContact}>
                      <p>ðŸ“§ {admissionsData.left.email}</p>
                      <p>ðŸ“ž {admissionsData.left.phone}</p>
                    </div>
                    <div className={styles.adCtas}>
                      {admissionsData.left.ctas.map((cta, idx) => (
                        <a
                          key={idx}
                          href={cta.href}
                          className={`${styles.cta} ${styles[cta.type]}`}
                        >
                          {cta.text}
                        </a>
                      ))}
                    </div>
                  </div>

                  {/* Middle */}
                  <div className={styles.adMiddle}>
                    <ul>
                      {admissionsData.middle.links.map((link, idx) => (
                        <li key={idx} className={styles.adLink}>
                          {link} â†’
                        </li>
                      ))}
                    </ul>
                    <div className={styles.adStats}>
                      <h3>{admissionsData.middle.stats.text}</h3>
                      <p>{admissionsData.middle.stats.subtext}</p>
                      <button className={styles.statsBtn}>
                        {admissionsData.middle.stats.btnText} â†’
                      </button>
                    </div>
                  </div>

                  {/* Right */}
                  <div className={styles.adRight}>
                    <Image
                      src={admissionsData.right.img}
                      alt={admissionsData.right.alt}
                      width={400}
                      height={400}
                      className={styles.adImg}
                    />
                  </div>
                </div>
              )}
            </div>

            {/* hamburger */}
            <button
              aria-label="Open menu"
              className={styles.hamburger}
              onClick={openMenu}
            >
              â˜°
            </button>
          </div>
        </div>
      </div>

      {/* backdrop */}
      <div
        className={`${styles.backdrop} ${menuOpen ? styles.show : ''}`}
        onClick={closeMenu}
      />

      {/* overlay */}
      <div className={`${styles.menuOverlay} ${menuOpen ? styles.open : ''}`} role="dialog">
        <button
          className={styles.closeBtn}
          aria-label="Close menu"
          onClick={closeMenu}
        >
          Ã—
        </button>

        <div className={styles.hamburgerLayout}>
          {/* left */}
          <aside className={styles.menuLeft}>
            <ul>
              {hamburgerMenudata.map((item, idx) => (
                <li
                  key={idx}
                  className={`${styles.menuLeftItem} ${
                    activeIndex === idx ? styles.active : ''
                  }`}
                  onClick={() => setActiveIndex(idx)}
                >
                  {item.name}
                </li>
              ))}
            </ul>
          </aside>

          {/* middle */}
          <section className={styles.menuMiddle}>
            <h4 className={styles.middleTitle}>
              ABOUT JSSMVP HERITAGE ABOUT JSS LEADERSHIP
            </h4>
            <ul>
              {activeData.subMenu.map((s, i) => (
                <li key={i} className={styles.middleItem}>
                  {s}
                </li>
              ))}
            </ul>
          </section>

          {/* right */}
          <section className={styles.menuRight}>
            <div className={styles.rightInner}>
              <div className={styles.imageBox}>
                <Image
                  src={activeData.img}
                  alt={activeData.name}
                  width={620}
                  height={400}
                  style={{ objectFit: 'cover' }}
                  sizes="100vw"
                />
              </div>
            </div>
          </section>
        </div>
      </div>
    </header>
  );
}